// File: src/assets/styles/page/modal.scss
.modal-overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(0, 0, 0, 0.5); // Na trong sut, ng b vi admin
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 1000;
  font-family: 'Poppins', sans-serif; // ng b font
}

.modal-content {
  background: #ffffff; // Nn trng ging admin__main
  padding: 20px; // Padding ging admin__form
  border-radius: 8px; // Bo gc ging admin__form
  width: 400px;
  max-width: 90%;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2); // Bng ging admin__image-preview
  animation: fadeIn 0.3s ease; // Hiu ng xut hin
}

.modal-title {
  font-size: 1.8rem; // Nh hn admin__section-title (2rem) nhng  ni bt
  font-weight: 600; // ng b vi admin__section-title
  color: #000000; // Mu ch ging admin__section-title
  margin-bottom: 10px;
}

.modal-message {
  font-size: 1.4rem; // Kch thc ch ging admin__form-field input
  color: #6b7280; // Mu xm ging admin__table td
  margin-bottom: 20px;
}

.modal-buttons {
  display: flex;
  justify-content: flex-end;
  gap: 10px; // Khong cch ging admin__form-row
}

.modal-btn {
  padding: 8px 16px; // Padding nh hn admin__form-button mt cht
  border: none;
  border-radius: 4px; // Bo gc ging admin__action-btn
  font-size: 1.4rem; // ng b vi admin__form-button
  font-weight: 500; // Font-weight nh hn mt cht
  cursor: pointer;
  transition:
    background-color 0.3s ease,
    transform 0.2s ease; // Hiu ng ging admin__add-button

  &--cancel {
    background: #ccc; // Mu xm nht
    color: #fff;
    &:hover {
      background: darken(#ccc, 10%);
      transform: translateY(-2px); // Hiu ng nhc ln ging admin__add-button
    }
    &:active {
      transform: translateY(0); // Tr li khi nhn
    }
  }

  &--confirm {
    background: #ff4d4d; // Mu  ging admin__logout v admin__action-btn--delete
    color: #fff;
    &:hover {
      background: darken(#ff4d4d, 10%);
      transform: translateY(-2px);
    }
    &:active {
      transform: translateY(0);
    }
  }
}

// Hiu ng fade in cho modal
@keyframes fadeIn {
  from {
    opacity: 0;
    transform: scale(0.95);
  }
  to {
    opacity: 1;
    transform: scale(1);
  }
}

// Responsive
@media (max-width: 768px) {
  .modal-content {
    width: 90%;
    padding: 15px; // Gim padding trn mobile
  }

  .modal-title {
    font-size: 1.6rem; // Gim kch thc ch
  }

  .modal-message {
    font-size: 1.3rem;
  }

  .modal-btn {
    padding: 6px 12px; // Gim kch thc nt
    font-size: 1.3rem;
  }
}
